@color_text_description: #00a452;
@color_option: #53a1ff;
@color_option: #4784ab;

@bgColor: #f8f8f8;
@borderColor: #d9e0e4;

body {

  .bxmaker__smsnotice__send {
    input[type="button"].bxmaker__smsnotice__send-box-content-btn {
      &.preloader,
      &.preloader:active {
        background-image: url("data:image/gif;base64,") !important;
        color: transparent !important;
        background-position: center center !important;
        background-repeat: no-repeat !important;
        text-shadow: none;
      }
    }

    &-box {
      border: 1px solid #d1dce0;
      margin-bottom: 30px;
      vertical-align: top;

      &-title {
        padding: 15px;
        background: #b9c7cd;
        font-weight: bold;
      }

      &-content {
        padding: 15px 15px;
        margin: 0;
        font-size: 14px;
        word-wrap: break-word;
        background: #fff;
        background: @bgColor;

        textarea {
          width: 100%;
          box-sizing: border-box;
          -webkit-box-sizing: border-box;
        }

        &-info {
          font-weight: normal;
          font-size: 0.9em;
          margin-bottom: 15px;
        }

        &-btn {
          width: 100%;
          font-size: 16px;
          padding: 10px;
          height: 40px;
        }

        &.bxmaker__smsnotice__send-box-content--filter {
          padding: 0 15px;
          border-bottom: 1px solid @borderColor;
        }
      }

      &--msg {
        width: 775px;
      }

      &.bxmaker__smsnotice__send-box--message {
        display: inline-block;
        position: relative;
        width: 370px;
        margin-right: 30px;

        .row_item {
          &.translit_box {
            font-size: 15px;
            padding: 14px 0 19px;
          }

          input[type="text"],
          textarea {
            width: 100%;
            box-sizing: border-box;
            padding: 10px 15px;
            margin-bottom: 15px;
          }

          input[type="text"] {
            height: 35px;

            &:hover,
            &:active {
              height: 35px;
            }

            &.preloader {
              background-image: url("data:image/gif;base64,") !important;
              color: transparent !important;
              background-position: center center !important;
              background-repeat: no-repeat !important;
              text-shadow: none;
            }
          }

          textarea {
            height: 110px;
          }

          select {
            width: 100%;
            margin-bottom: 15px;
            height: 35px;
          }
        }

        .info_box {
          height: 18px;

          .text_size {
            float: left;
            font-size: 12px;
            color: #737171;
          }
          .btn_clean {
            float: right;
            font-size: 14px;
            top: -3px;
            position: relative;
            cursor: pointer;
            color: #3d69b4;
            border-bottom: 1px dashed #3d69b4;
          }
        }

        // 
        .result_msg_text {
          position: absolute;
          left: 400px;
          border: 1px solid #d1dce0;
          top: 0;
          width: 250px;

          .title_box {
            padding: 15px;
            background: #b9c7cd;
            font-weight: bold;
          }

          .text_box {
            padding: 15px 15px 30px;
            background: #fff;
            margin: 0;
            font-size: 14px;
            word-wrap: break-word;
          }
        }
      }

      &.bxmaker__smsnotice__send-box--phone {
        display: inline-block;
        position: relative;
        width: 370px;

        &-active {
          .bxmaker__smsnotice__send-filter-row {
            display: block !important;
          }
        }
      }

      &--error {
        padding: 15px;
        margin: 15px 0;
        display: none;
        background: rgb(250, 250, 250);

        .close {
          cursor: pointer;
          border-bottom: 1px dashed #53a1ff;
          display: inline-block;
          float: right;
          margin-right: 0;
          margin-bottom: 5px;

          &:hover {
            border-color: transparent;
          }
        }

        .descr {
          margin-top: 20px;
          //max-height: 270px;
          display: block;
          box-sizing: border-box;
          max-width: 100%;
          width: auto;
        }
      }
    }

    &-msg {
      top: 0;
      width: 770px;

      .msg_box {
        padding: 15px;
        display: none;

        &.success {
          display: block;
          background-color: fade(rgba(212, 255, 228, 0.82), 60%);
          border: 1px solid #00cf68;
        }

        &.error {
          display: block;
          background-color: fade(#ffdcdc, 60%);
          border: 1px solid #cf1d00;
        }

        &.extend {
          display: block;
          padding: 0;

          .item_success {
            display: block;
            background-color: fade(rgba(212, 255, 228, 0.82), 60%);
            border: 1px solid #00cf68;
            padding: 5px;
            margin-bottom: 3px;
          }
          .item_error {
            display: block;
            background-color: fade(#ffdcdc, 60%);
            border: 1px solid #cf1d00;
            padding: 5px;
            margin-bottom: 3px;
          }
        }
      }

    }

    &-filter {

      padding: 0 15px;
      border-bottom: 1px solid @borderColor;

      &-view {
        text-align: center;
        padding: 15px;
        font-size: 0.9em;

        span {
          border-bottom: 1px dashed @color_option;
          color: @color_option;
          cursor: pointer;
        }

        &:hover {
          span {
            border-bottom-color: transparent;
          }
        }
      }

      &-row {
        display: none;
        margin-bottom: 15px;

        &-label {
          width: 166px;
          display: inline-block;
          font-weight: bold;
          color: #565656;
        }
        &-value {
          width: 170px;
          display: inline-block;

          select {
            width: 170px !important;
            box-sizing: border-box;
          }
        }

        input[type="button"] {
          margin-top: 0;
        }

      }
    }

    &-list {

      &-error {
        cursor: pointer;

        &-btn {
          border-bottom: 1px dashed blue;
          color: blue;

          &:hover {
            border-color: transparent;
          }
        }

        .more {
          display: none;
        }
      }

      &-status {
        text-align: center;
        margin-left: -16px;

        &--wait,
        &--sent,
        &--delivered,
        &--error {
          border-radius: 10px;
          padding: 2px 10px 4px;
          color: #fff;
        }

        &--sent {
          background: #dde260;
          color: #454545;
        }
        &--wait {
          background: #8caae2;
        }

        &--delivered {
          background: #55e251;
        }

        &--error {
          background: #ff5a5f;
        }
      }
    }
  }

  // redaktirovanie shablona soobtsheniya
  form[name="bxmaker__smsnotice__template__edit"] {
    select,
    input[type="text"] {
      width: 300px !important;
      box-sizing: border-box;
    }

    textarea {
      width: 100%;
      box-sizing: border-box;
    }

    .template_fields_box {
      padding: 15px;
      line-height: 1.5;

      span {
        cursor: pointer;
        color: #53a1ff;
        border-bottom: 1px dashed #53a1ff;

        &:hover {
          border-color: transparent;
        }
      }

      small {
        color: @color_text_description;
      }

    }

    .req {
      color: red;
    }

    span.order_status_name {
      color: @color_text_description;
    }

    .event_type_box {
      display: none;

      &.active {
        display: table-row;
      }
    }

    .type_box {
      &.hide {
        display: none;
      }
    }

    .conditions_box {
      text-align: center;
    }
  }

  // redaktirovanie servisa
  form[name="bxmaker_smsnotice_service_edit_form"] {
    .req {
      color: red;
    }

    p {
      line-height: 1.5;
    }
  }

  .ap-bxmaker_smsnotice-notice-box {
    padding: 15px;
    margin: 15px 0;
    border: 1px solid @color_option;
    background: #f8f8f9;

    &.expired {
      border: 1px solid #CA3D3D;
      background: #F9F5F5;
    }
  }
}

.bxmaker__smsnotice__template__conditions {
  &-area {
    display: block;
    text-align: left;
  }
}

.bxmaker-smsnotice-log {
  &__text {
    display: none;

    &&--show {
      display: inline-block;
    }
  }

  &__preview {
    display: inline-block;

    &&--hide {
      display: none;
    }
  }

  &__more {
    color: blue;
    border-bottom: 1px dashed;
    cursor: pointer;
  }
}

.bxmaker-smsnotice-order-sms {
  display: block;
  min-height: 100px;
}

//    
.bxmaker-smsnotice-service-edit {
  input {
    max-width:200px !important;
  }
  select {
    max-width:200px !important;
    min-width: auto !important;
    width: auto !important;
  }
}

.bxmaker-smsnotice-service-edit-params {
  &__text {
    display: block;
    margin-bottom: 15px;
    padding: 15px;
    background: #f6f193;
    border-radius: 1px;
    border: 1px solid #e8e392;

    p {
      margin: 0;
      padding: 0;
    }
  }
}

.bxmaker-smsnotice-service-edit-param {
  display: block;
  position: relative;
  margin-bottom: 15px;

  &__title {
    display: inline-block;
    width: 150px;
    font-weight: bold;
    text-align: right;
    padding: 0 15px 0 0;
    vertical-align:top;
  }
  &__value {
    display: inline-block;
    width: 250px;

  }
}

//  
.bxmaker-smsnotice-admin-order {
  .bxmaker-smsnotice-admin-send {
    margin: 0 15px 15px 0;
  }
}

.bxmaker-smsnotice-admin-input {
  .adm-workarea input&[type="text"],
  .adm-workarea input&[type="email"],
  .adm-workarea input&[type="password"] {
    background: #fefefe;
    line-height: 20px;
    text-align: left;
    border: 1px solid #d1dce0;
    border-radius: 2px;
    color: #383838;
    height: auto;
    font-size: 13px;
    padding: 8px 10px 7px;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    box-shadow: inset 0 2px 3px -1px rgba(180, 188, 191, 0.3);

    &&--defaul-page {
      border: 1px solid #bfcccf;
    }
  }
}

.bxmaker-smsnotice-admin-textarea {
  .adm-workarea & {
    background: #fefefe;
    line-height: 20px;
    text-align: left;
    border: 1px solid #d1dce0;
    border-radius: 2px;
    color: #383838;
    height: auto;
    font-size: 13px;
    padding: 8px 10px;
    width: 100%;
    min-width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    box-shadow: inset 0 2px 3px -1px rgba(180, 188, 191, 0.3);
  }
}

.bxmaker-smsnotice-admin-select {
  .adm-workarea & {
    background: #fefefe;
    line-height: 20px;
    text-align: left;
    border: 1px solid #d1dce0;
    border-radius: 2px;
    color: #383838;
    height: auto;
    font-size: 13px;
    padding: 8px 20px 10px 5px;
    width: 100%;
    min-width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    box-shadow: inset 0 2px 3px -1px rgba(180, 188, 191, 0.3);

  }
  .adm-workarea &&--defaul-page {
    border: 1px solid #bfcccf;
  }

  .adm-workarea &&--autowidth {
    width: auto;
    min-width: auto;
    max-width: none;
  }
}

.bxmaker-smsnotice-admin-row {
  padding: 0 15px;
  margin-bottom: 15px;

  &:after {
    display: block;
    content: ' ';
    float: none;
    clear: both;
  }

  &&--center {
    text-align: center;
  }

  &__title {
    font-size: 0.9em;
    display: block;
    margin-bottom: 5px;
  }

  &__value {

  }
}

.bxmaker-smsnotice-admin-btn {
  display: inline-block;

  &&--button&--preloader {
    background-image: url(data:image/svg+xml;base64,);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 80% 80%;
    color: transparent;

    &::selection {
      background: transparent;
    }
  }

  &&--button {
    padding: 12px 30px;
    border-radius: 2px;
    color: #444;
    cursor: pointer;
    background-color: #edeff1;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    border: 1px solid #ddd;
    text-transform: uppercase;
    font-size: 0.85em;
    font-weight: 700;

    &:hover {
      background-color: #f4f7f9;
    }

    &:active {
      background-color: #fff;
    }
  }

  &&--line {
    padding: 0;
    font-size: 14px;
    top: -3px;
    position: relative;
    cursor: pointer;
    color: #3d69b4;
    border-bottom: 1px dashed #3d69b4;
  }

  &&--success {
    color: #fff;
    background-color: #8bc34a;
    border-color: darken(#8bc34a, 5%);

    &:hover {
      background-color: lighten(#8bc34a, 5%);
    }
    &:active {
      background-color: lighten(#8bc34a, 10%);
    }
  }
}

.bxmaker-smsnotice-admin-status {
  text-align: center;
  display: inline-block;

  &--wait,
  &--sent,
  &--delivered,
  &--error {
    border-radius: 10px;
    padding: 2px 10px 4px;
    color: #fff;
  }

  &--sent {
    background: #dde260;
    color: #454545;
  }
  &--wait {
    background: #8caae2;
  }

  &--delivered {
    background: #55e251;
  }

  &--error {
    background: #ff5a5f;
  }
}