.bxmaker-smsnotice-admin-history {
  color: #444;
  background: #fff;
  border: 1px solid #d1dce0;

  vertical-align: top;
  display: inline-block;
  position: relative;
  width: 370px;
  margin: 0;
  font-size: 14px;
  word-wrap: break-word;
  border-radius: 2px;

  &__title {
    padding: 15px;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 0.85em;
    border-bottom: 1px solid #e8e8e8;
    background: #5f6673;
    color: #fff;
    margin-top: -1px;
    border-radius: 2px 2px 0 0;
  }

  &__container {

  }

  &__empty {
    padding: 30px 15px;
    text-align: center;
    color: #888;
  }

}

.bxmaker-smsnotice-admin-history-item {

  padding: 5px 15px;
  border-bottom: 1px solid #ddd;

  .bxmaker-smsnotice-admin-status {
    width: 10px;
    font-size: 0;
    padding: 0;
    height: 10px;
    vertical-align: middle;
    line-height: 1;
    margin-right: 5px;
  }

  &__time {
    font-size: 0.85em;
    display: inline-block;
    margin-right: 5px;
    color: #727272;
  }
  &__type {
    font-size: 0.85em;
    display: inline-block;
    margin-right: 5px;
    color: #727272;
  }

  &__text {
    line-height: 1.5;
    color: #444;
    padding: 5px 0;
    font-style: italic;
  }
}