


.bxmaker-smsnotice-admin-send {
    color: #444;
    background: #fff;
    border: 1px solid #d1dce0;

    vertical-align: top;
    display: inline-block;
    position: relative;
    width: 370px;
    margin: 0;
    font-size: 14px;
    word-wrap: break-word;
    border-radius:2px;

    &__title {
        padding: 15px;
        font-weight: 700;
        text-transform: uppercase;
        font-size: 0.85em;
        border-bottom: 1px solid #e8e8e8;
        background: #5f6673;
        color: #fff;
        margin-top: -1px;
        border-radius:2px 2px 0 0;
    }

    &__container {
        padding:15px 0 0;
    }

    &__message {
        margin:0 15px 15px;
    }

    &__limit {
        float:left;
        max-width: 270px;
        font-size: 0.9em;
        color: #999
    }

    &__clean {
        float: right;
    }

    &__translit {
        display: inline-block;
        margin-top: 15px;
    }

    &__preview {
        border: 1px solid #f5f4e0;
        background: #fbfae2;
        font-size: 0.9em;
        padding: 10px 15px;
        line-height: 1.5;
    }

}
