.bxmaker-smsnotice-admin-info {
    display: none;
    padding: 15px;
    border: 1px solid #ddd;

    &&--success {
        display: block;
        border-color: #d6f3d7;
        background: rgba(0, 255, 22, 0.1);
        color: #034f21;

    }

    &&--error {
        display: block;
        border-color:#f3d6d6;
        background: rgba(255, 0, 0, 0.1);
        color: #590202;
    }
}